<template>
  <router-view/>
</template>
<script>
import { mapMutations, mapActions } from 'vuex'
import { getToken, setToken, setColor } from 'utils/tool'
import { getTheme } from 'api/setting'
import defaultColor from '@/styles/default_color.json'

export default {
  name: 'Index',
  mounted () {
    // 全局页面判断
    let newToken = this.$route.query.token
    if (newToken) {
      setToken(newToken)
      this.setToken(newToken)
      this.$router.replace({name: 'Home'})
    } else {
      let nowToken = getToken()
      if (nowToken) {
        this.setToken(nowToken)
      } else {
        // query上没有，cookie上也没有，跳转到错误提示页
        this.$router.replace({name: 'Error'})
        return
      }
    }
    // get cssText
    let styleElement = document.getElementById('style-css')
    let cssText = styleElement.innerText
    const colorMap = defaultColor
    Object.keys(colorMap).forEach(key => {
      const value = colorMap[key]
      cssText = cssText.replace(new RegExp(key, 'ig'), value)
    })
    window.cssText = cssText
    // get theme
    getTheme({}).then(res => {
      if (Number(res.code) === 200 && res.data) {
        setColor(res.data)
        setTimeout(() => {
          this.hideLoading()
        }, 400)
      } else {
        this.hideLoading()
      }
    }).catch(() => {
      this.hideLoading()
    })
    // get approval num
    this.getApprovalNum()
  },
  methods: {
    hideLoading () {
      let loading = document.getElementById('loading')
      if (loading) {
        loading.classList.add('loading-hide')
        setTimeout(function () {
          loading.parentNode.removeChild(loading)
        }, 400)
      }
    },
    ...mapActions([
      'getApprovalNum'
    ]),
    ...mapMutations({
      setToken: 'SET_TOKEN'
    })
  }
}
</script>



